import * as React from "react"
import { Link, graphql } from "gatsby"

import Bio from "../components/bio"
import Layout from "../components/layout"
import Seo from "../components/seo"
import TableOfContents from "../components/toc"

const BlogPostTemplate = ({
  data: { previous, next, site, markdownRemark: post },
  location,
}) => {
  const siteTitle = site.siteMetadata?.title || `Title`

  React.useEffect(() => {
    const progressBar = document.createElement('div')
    progressBar.className = 'scroll-progress'
    document.body.appendChild(progressBar)

    const updateProgress = () => {
      const scrollTop = window.pageYOffset
      const docHeight = document.body.scrollHeight - window.innerHeight
      const scrollPercent = (scrollTop / docHeight) * 100
      progressBar.style.width = scrollPercent + '%'
    }

    window.addEventListener('scroll', updateProgress)
    
    return () => {
      window.removeEventListener('scroll', updateProgress)
      if (document.body.contains(progressBar)) {
        document.body.removeChild(progressBar)
      }
    }
  }, [])

  return (
    <Layout location={location} title={siteTitle}>
      <div className="blog-post-layout">
        <div className="blog-post-container">
          <article
            className="blog-post"
            itemScope
            itemType="http://schema.org/Article"
          >
            <header>
              <h1>{post.frontmatter.title}</h1>
              <p className="post-date">{post.frontmatter.date}</p>
              {post.frontmatter.series && (
                <div className="series-info">
                  <Link to={`/series/${(() => {
                    const map = {
                      'ë§ê¸€ íŠœí„°ë§ ë³µìŠµ': 'ringle-tutoring-review',
                      'ì…€ëŸ½ì‡ í”„ë¡œì íŠ¸': 'celuveat-project', 
                      'ê·¸ë¦¼ìœ¼ë¡œ ë°°ìš°ëŠ” Http & Network Basic': 'http-network-basic',
                      'ì°½êµ¬ AI ìŠ¤í„°ë”” ì¼': 'changgu-ai-study-jam',
                      'ìš´ì˜ì²´ì œ ë©´ì ‘ ìŠ¤í„°ë””': 'os-interview-study',
                      'Why? ì‹œë¦¬ì¦ˆ': 'why-series',
                      'DB ë©´ì ‘ ìŠ¤í„°ë””': 'db-interview-study',
                      'Real MySQL 8.0': 'real-mysql-8'
                    }
                    return map[post.frontmatter.series] || post.frontmatter.series.toLowerCase().replace(/[^a-z0-9]/g, '-').replace(/-+/g, '-').replace(/^-|-$/g, '')
                  })()}`} className="series-link">
                    ğŸ“š {post.frontmatter.series} ì‹œë¦¬ì¦ˆ
                  </Link>
                </div>
              )}
              <p>{post.frontmatter.tags && post.frontmatter.tags.length > 0 && (
                <div className="tags-container">
                  {post.frontmatter.tags.map(tag => (
                    <span key={tag} className="tag">
                      #{tag}
                    </span>
                  ))}
                </div>
              )}</p>
            </header>
            <section
              className="blog-post-content"
              dangerouslySetInnerHTML={{ __html: post.html }}
              itemProp="articleBody"
            />
            <hr />
            <footer>
              <Bio />
            </footer>
          </article>
        </div>
        {post.tableOfContents && <TableOfContents tableOfContents={post.tableOfContents} />}
      </div>
      <nav className="blog-post-nav">
        <ul>
          <li>
            {previous && (
              <Link to={previous.fields.slug} rel="ì´ì „ ê¸€">
                {previous.frontmatter.title}
              </Link>
            )}
          </li>
          <li>
            {next && (
              <Link to={next.fields.slug} rel="ë‹¤ìŒ ê¸€">
                {next.frontmatter.title}
              </Link>
            )}
          </li>
        </ul>
      </nav>
    </Layout>
  )
}

export const Head = ({ data: { markdownRemark: post } }) => {
  return (
    <Seo
      title={post.frontmatter.title}
      description={post.frontmatter.description || post.excerpt}
    />
  )
}

export default BlogPostTemplate

export const pageQuery = graphql`
  query BlogPostBySlug(
    $id: String!
    $previousPostId: String
    $nextPostId: String
  ) {
    site {
      siteMetadata {
        title
      }
    }
    markdownRemark(id: { eq: $id }) {
      id
      excerpt(pruneLength: 160)
      html
      tableOfContents
      frontmatter {
        title
        date(formatString: "YYYYë…„ Mì›” Dì¼")
        description
        tags
        series
      }
    }
    previous: markdownRemark(id: { eq: $previousPostId }) {
      fields {
        slug
      }
      frontmatter {
        title
      }
    }
    next: markdownRemark(id: { eq: $nextPostId }) {
      fields {
        slug
      }
      frontmatter {
        title
      }
    }
  }
`
