import React, { useEffect, useState, useRef, useCallback } from 'react'

const TableOfContents = ({ tableOfContents }) => {
  const [activeId, setActiveId] = useState('')
  const [headings, setHeadings] = useState([])
  const tocRef = useRef(null)

  // TOCì—ì„œ ë§í¬ ID ì¶”ì¶œí•˜ê¸° (URL ë””ì½”ë”© í¬í•¨)
  const extractTocIds = useCallback((tocHtml) => {
    if (!tocHtml) return []
    
    const tempDiv = document.createElement('div')
    tempDiv.innerHTML = tocHtml
    const links = tempDiv.querySelectorAll('a[href^="#"]')
    
    return Array.from(links).map(link => {
      const href = link.getAttribute('href')
      if (!href) return null
      
      const encodedId = href.substring(1)
      
      // URL ë””ì½”ë”© ì‹œë„
      try {
        const decodedId = decodeURIComponent(encodedId)
        console.log(`ID ë³€í™˜: ${encodedId} -> ${decodedId}`)
        return { encoded: encodedId, decoded: decodedId }
      } catch (e) {
        console.warn(`URL ë””ì½”ë”© ì‹¤íŒ¨: ${encodedId}`, e)
        return { encoded: encodedId, decoded: encodedId }
      }
    }).filter(Boolean)
  }, [])

  // í—¤ë”© ìš”ì†Œë“¤ì„ ì°¾ê³  ì €ì¥
  useEffect(() => {
    if (!tableOfContents) return

    const findHeadings = () => {
      console.log('=== TOC Debug Start ===')
      console.log('TableOfContents HTML:', tableOfContents)
      
      // TOCì—ì„œ ì–¸ê¸‰ëœ IDë“¤ì„ ì¶”ì¶œ
      const tocIdObjects = extractTocIds(tableOfContents)
      console.log('TOCì—ì„œ ì¶”ì¶œëœ ID ê°ì²´ë“¤:', tocIdObjects)

      // í˜ì´ì§€ì˜ ëª¨ë“  í—¤ë”© ìš”ì†Œë“¤ í™•ì¸
      const allHeadings = document.querySelectorAll('h1, h2, h3, h4, h5, h6')
      console.log('í˜ì´ì§€ì˜ ëª¨ë“  í—¤ë”©ë“¤:')
      allHeadings.forEach((heading, index) => {
        console.log(`${index + 1}. ID: "${heading.id}", Text: "${heading.textContent.trim()}", Tag: ${heading.tagName}`)
      })

      // ì‹¤ì œ DOMì—ì„œ í•´ë‹¹ IDë¥¼ ê°€ì§„ í—¤ë”© ìš”ì†Œë“¤ ì°¾ê¸°
      const headingElements = []
      
      tocIdObjects.forEach(idObj => {
        // ì¸ì½”ë”©ëœ IDì™€ ë””ì½”ë”©ëœ ID ëª¨ë‘ ì‹œë„
        let element = document.getElementById(idObj.encoded)
        if (!element) {
          element = document.getElementById(idObj.decoded)
        }
        
        if (element) {
          console.log(`ID "${idObj.encoded}" ë˜ëŠ” "${idObj.decoded}"ì— í•´ë‹¹í•˜ëŠ” ìš”ì†Œ ì°¾ìŒ:`, element.tagName, element.textContent.trim())
          if (element.matches('h1, h2, h3, h4, h5, h6')) {
            headingElements.push(element)
          } else {
            console.warn(`ID "${idObj.encoded}"ëŠ” í—¤ë”© ìš”ì†Œê°€ ì•„ë‹™ë‹ˆë‹¤:`, element.tagName)
          }
        } else {
          console.warn(`ID "${idObj.encoded}" ë˜ëŠ” "${idObj.decoded}"ì— í•´ë‹¹í•˜ëŠ” ìš”ì†Œë¥¼ ì°¾ì„ ìˆ˜ ì—†ìŠµë‹ˆë‹¤`)
        }
      })

      // ì¶”ê°€ë¡œ TOC IDê°€ ì—†ì§€ë§Œ ì‹¤ì œ ì¡´ì¬í•˜ëŠ” í—¤ë”©ë“¤ë„ ì°¾ê¸°
      const allHeadingsWithIds = Array.from(allHeadings).filter(h => h.id)
      console.log('IDê°€ ìˆëŠ” ëª¨ë“  í—¤ë”©ë“¤:', allHeadingsWithIds.map(h => ({ id: h.id, text: h.textContent.trim() })))
      
      // TOCì— ì—†ì§€ë§Œ ì‹¤ì œë¡œ ì¡´ì¬í•˜ëŠ” í—¤ë”©ë“¤ì„ í¬í•¨
      allHeadingsWithIds.forEach(heading => {
        if (!headingElements.includes(heading)) {
          console.log(`TOCì— ì—†ì§€ë§Œ ì‹¤ì œ ì¡´ì¬í•˜ëŠ” í—¤ë”© ì¶”ê°€: ${heading.id} - "${heading.textContent.trim()}"`)
          headingElements.push(heading)
        }
      })
      
      // ë¬¸ì„œì—ì„œì˜ ìˆœì„œëŒ€ë¡œ ì •ë ¬
      headingElements.sort((a, b) => {
        const rectA = a.getBoundingClientRect()
        const rectB = b.getBoundingClientRect()
        return rectA.top - rectB.top
      })
      
      console.log('ìµœì¢… ê°ì§€ëœ í—¤ë”©ë“¤:', headingElements.map(h => ({ id: h.id, text: h.textContent.trim() })))
      console.log('=== TOC Debug End ===')
      
      setHeadings(headingElements)
      
      // ì²« ë²ˆì§¸ í—¤ë”©ì„ ì´ˆê¸° í™œì„± ìƒíƒœë¡œ ì„¤ì •
      if (headingElements.length > 0) {
        setActiveId(headingElements[0].id)
      }
    }

    // DOM ë¡œë“œ ì™„ë£Œ í›„ í—¤ë”© ì°¾ê¸°
    const timer = setTimeout(findHeadings, 500)
    
    return () => clearTimeout(timer)
  }, [tableOfContents, extractTocIds])

  // ìŠ¤í¬ë¡¤ ì´ë²¤íŠ¸ ì²˜ë¦¬
  useEffect(() => {
    if (headings.length === 0) return

    const handleScroll = () => {
      const scrollY = window.pageYOffset
      let currentActiveId = ''

      // í˜„ì¬ í™”ë©´ì—ì„œ ê°€ì¥ ê°€ê¹Œìš´ í—¤ë”© ì°¾ê¸°
      let closestHeading = null
      let closestDistance = Infinity

      headings.forEach(heading => {
        const rect = heading.getBoundingClientRect()
        const elementTop = rect.top
        
        // í—¤ë”©ì´ í™”ë©´ ìƒë‹¨ ê·¼ì²˜ì— ìˆëŠ” ê²½ìš° ìš°ì„  ê³ ë ¤
        if (elementTop <= 200 && elementTop >= -100) {
          const distance = Math.abs(elementTop - 100)
          if (distance < closestDistance) {
            closestDistance = distance
            closestHeading = heading
          }
        }
      })

      // ê°€ê¹Œìš´ í—¤ë”©ì´ ì—†ìœ¼ë©´ ìŠ¤í¬ë¡¤ ìœ„ì¹˜ ê¸°ë°˜ìœ¼ë¡œ ì°¾ê¸°
      if (!closestHeading) {
        for (let i = headings.length - 1; i >= 0; i--) {
          const heading = headings[i]
          const rect = heading.getBoundingClientRect()
          const offsetTop = rect.top + scrollY
          
          if (offsetTop <= scrollY + 150) {
            closestHeading = heading
            break
          }
        }
      }

      // ì—¬ì „íˆ ì—†ìœ¼ë©´ ì²« ë²ˆì§¸ í—¤ë”©
      if (!closestHeading && headings[0]) {
        closestHeading = headings[0]
      }

      if (closestHeading) {
        currentActiveId = closestHeading.id
      }

      if (currentActiveId !== activeId) {
        console.log(`Active ID ë³€ê²½: ${activeId} -> ${currentActiveId}`)
        setActiveId(currentActiveId)
      }
    }

    // ìŠ¤í¬ë¡¤ ì´ë²¤íŠ¸ ë“±ë¡ (ë””ë°”ìš´ìŠ¤ ì ìš©)
    let ticking = false
    const throttledScroll = () => {
      if (!ticking) {
        requestAnimationFrame(() => {
          handleScroll()
          ticking = false
        })
        ticking = true
      }
    }

    window.addEventListener('scroll', throttledScroll, { passive: true })
    window.addEventListener('resize', throttledScroll, { passive: true })
    
    // ì´ˆê¸° ì‹¤í–‰
    handleScroll()

    return () => {
      window.removeEventListener('scroll', throttledScroll)
      window.removeEventListener('resize', throttledScroll)
    }
  }, [headings, activeId])

  // TOC ë§í¬ í´ë¦­ ì²˜ë¦¬ (URL ë””ì½”ë”© í¬í•¨)
  const handleTocClick = useCallback((e) => {
    if (e.target.tagName !== 'A') return
    
    e.preventDefault()
    
    const href = e.target.getAttribute('href')
    if (!href || !href.startsWith('#')) return

    const encodedId = href.substring(1)
    console.log(`TOC ë§í¬ í´ë¦­: ${encodedId}`)
    
    // URL ë””ì½”ë”© ì‹œë„
    let targetElement = document.getElementById(encodedId)
    
    if (!targetElement) {
      try {
        const decodedId = decodeURIComponent(encodedId)
        console.log(`ë””ì½”ë”©ëœ IDë¡œ ì¬ì‹œë„: ${decodedId}`)
        targetElement = document.getElementById(decodedId)
      } catch (e) {
        console.warn(`URL ë””ì½”ë”© ì‹¤íŒ¨: ${encodedId}`, e)
      }
    }
    
    if (targetElement) {
      const offsetTop = targetElement.getBoundingClientRect().top + window.pageYOffset - 100
      
      window.scrollTo({
        top: offsetTop,
        behavior: 'smooth'
      })
      
      // ì¦‰ì‹œ í™œì„±í™” (ì‹¤ì œ DOM ID ì‚¬ìš©)
      setActiveId(targetElement.id)
    } else {
      console.warn(`í´ë¦­í•œ ë§í¬ì˜ ëŒ€ìƒ ìš”ì†Œë¥¼ ì°¾ì„ ìˆ˜ ì—†ìŒ: ${encodedId}`)
    }
  }, [])

  // TOC HTMLì—ì„œ active í´ë˜ìŠ¤ ì ìš© (URL ì¸ì½”ë”© ê³ ë ¤)
  const processedTableOfContents = React.useMemo(() => {
    if (!tableOfContents) return tableOfContents

    let processed = tableOfContents

    // ëª¨ë“  ë§í¬ì—ì„œ active í´ë˜ìŠ¤ ì œê±°
    processed = processed.replace(/\s*class="[^"]*active[^"]*"/g, '')
    processed = processed.replace(/\s*class=""/g, '')

    // í˜„ì¬ í™œì„± IDì—ë§Œ active í´ë˜ìŠ¤ ì¶”ê°€
    if (activeId) {
      // ì¸ì½”ë”©ëœ í˜•íƒœë¡œë„ ì°¾ê¸°
      let foundMatch = false
      
      try {
        const encodedActiveId = encodeURIComponent(activeId)
        
        // ì¸ì½”ë”©ëœ IDë¡œ ë¨¼ì € ì‹œë„
        const encodedRegex = new RegExp(`(<a[^>]*href="#${encodedActiveId.replace(/[.*+?^${}()|[\]\\]/g, '\\$&')}"[^>]*)(>)`, 'g')
        if (processed.match(encodedRegex)) {
          processed = processed.replace(encodedRegex, '$1 class="active"$2')
          console.log(`Active í´ë˜ìŠ¤ ì ìš© (ì¸ì½”ë”©): ${encodedActiveId}`)
          foundMatch = true
        }
      } catch (e) {
        console.warn(`URL ì¸ì½”ë”© ì‹¤íŒ¨: ${activeId}`, e)
      }
      
      // ì¸ì½”ë”©ëœ í˜•íƒœë¡œ ì°¾ì§€ ëª»í–ˆìœ¼ë©´ ì›ë³¸ IDë¡œ ì‹œë„
      if (!foundMatch) {
        const escapedId = activeId.replace(/[.*+?^${}()|[\]\\]/g, '\\$&')
        const regex = new RegExp(`(<a[^>]*href="#${escapedId}"[^>]*)(>)`, 'g')
        processed = processed.replace(regex, '$1 class="active"$2')
        console.log(`Active í´ë˜ìŠ¤ ì ìš© (ì›ë³¸): ${activeId}`)
      }
    }

    return processed
  }, [tableOfContents, activeId])

  if (!tableOfContents) return null

  return (
    <aside className="toc-container" ref={tocRef}>
      <h3 className="toc-title">ëª©ì°¨</h3>
      <div 
        className="toc-content"
        onClick={handleTocClick}
        dangerouslySetInnerHTML={{ __html: processedTableOfContents }} 
      />
    </aside>
  )
}

export default TableOfContents